body {
  font-family: Roboto, sans-serif;
  margin: 2rem;
}

.container {
  max-width: 300px;
  margin: 0 auto;
}

.anime {
  opacity: 0;
  transform: translateX(-20px);
  animation: anime 0.5s forwards;
}

@keyframes anime {
  to {
    opacity: 1;
    transform: initial;
  }
}
